	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2013-02-20-font-awesome-or-any-other-webfont-in-production/index.md absPath of file >>> MarkdownRemark","html":"<p>If you are using <a href=\"http://twitter.github.com/bootstrap/\">Twitter Bootstrap</a> then it's likely you're also using <a href=\"http://fortawesome.github.com/Font-Awesome/\">Font Awesome</a>. If not you need to check it out.</p>\n<p>We ran into a problem after deploying to production that we didn't observe in development - namely that none of the Font Awesome icons would show up under Firefox. <a href=\"http://serverfault.com/questions/186965/how-can-i-make-nginx-support-font-face-formats-and-allow-access-control-allow-o\">Turns you you need to configure your server to send the proper headers and allow cross domain requests</a>. Also check out <a href=\"http://www.red-team-design.com/firefox-doesnt-allow-cross-domain-fonts-by-default\">Firefox doesn't allow cross domain fonts</a>.</p>","frontmatter":{"title":"Font Awesome (or any other webfont in production)","date":"February 20, 2013","imageUrl":null,"image":null}}},"pathContext":{"path":"/font-awesome-or-any-other-webfont-in-production"}}

